.input, .input-timed .vdp-datepicker input {
  width: 100%;
  margin: 0px;
  padding: 6px 18px;
  border-radius: 7.5px;
  border: 1px solid #e0e0e0;
  outline: 2px solid transparent;
  transition: all .182s ease-out;
}
.input-tip {
  transition: all .242s ease-out;
  padding: 0px 18px;
  padding-top: 2px;
  height: 0px;
  overflow: hidden;
}
.ip-tip-active {
  height: 1.5em;
  animation: shower .62s ease-out;
}
@keyframes shower {
0% {
    height: 0px;
}
100% {
    height: 1.5em;
}
}
.input-pri:hover, .input-pri:focus, .input-pri:active, .input-pri:focus-visible {
  border-color: #cb264a;
  transition: all .242s ease-out;
  box-shadow: 0px 0px 4px -0.5px #ec768f;
}
.input-air {
  background: none;
}
.input-air:hover {
    border-color: #c2c2c2;
    background: rgba(255, 255, 255, 0.86);
}
.inline-search input {
  width: 412px;
}
.inline-search button {
  width: 10em;
  margin-left: 12px;
  min-height: 38px !important;
}
.input-attach {
  position: relative;
}
.input-attach .icon {
    top: 48%;
    right: 1em;
    font-size: 1.4em;
    position: absolute;
    color: #bababa;
    transform: translateY(-50%);
}
.input-attach input {
    padding-right: 2.6em;
}
.input-timed .vdp-datepicker__calendar {
  border-color: transparent;
  overflow: hidden;
  border-radius: 6px;
  padding-bottom: 2px;
  box-shadow: 0px 5px 8px -1px rgba(0, 0, 0, 0.18);
}
.input-timed .vdp-datepicker__calendar span.day {
    border-radius: 100% !important;
}
.input-timed .vdp-datepicker__calendar span.year,
  .input-timed .vdp-datepicker__calendar span.month {
    border-radius: 4px !important;
}
.input-timed .vdp-datepicker__calendar .selected {
    color: #FFF;
    background: #18979c !important;
}
.input-timed .vdp-datepicker__calendar .prev,
  .input-timed .vdp-datepicker__calendar .next,
  .input-timed .vdp-datepicker__calendar .day__month_btn,
  .input-timed .vdp-datepicker__calendar .month__year_btn {
    padding: 8px 0px;
    text-align: center;
}
.input-timed .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).day:hover,
  .input-timed .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).month:hover,
  .input-timed .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).year:hover {
    border: 1px solid #1aa3a8;
    background: none;
}
.input-inset {
  position: relative;
}
.input-inset .input-inset-wrapper {
    z-index: 333;
    padding-left: 7.2em;
}
.input-inset .input-inset-in {
    width: 6em;
    top: 0px;
    left: 0;
    z-index: 444;
    padding: 0px 12px;
    position: absolute;
    border: none !important;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}
.checkbox {
  border: 1px solid #c2c2c2 !important;
}
.input-icon {
  position: relative;
}
.input-icon i {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
}
.input-icon .input, .input-icon .input-timed .vdp-datepicker input, .input-timed .vdp-datepicker .input-icon input {
    padding-right: 3em;
}
.input-disable i {
  opacity: 0.12;
}
.input-disable input {
  cursor: not-allowed;
